
/* src/Pages/navbar.css */

.navbar {
    background-color: #333;
    overflow: hidden;
  }
  
  .navbar-menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  .navbar-menu li {
    float: left;
  }
  
  .navbar-menu li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 20px;
    text-decoration: none;
  }
  
  .navbar-menu li a:hover {
    background-color: #ddd;
    color: black;
  }
  